	data() {
		return {
			classname: 'box',
			title: 'hellowda',
			isActivity: true,
			age: 18,
			isshow: false,
			weight : 2000
		}
	},
	onLoad() {

	},
	computed: {
		myconvert:function() {
			if(this.weight > 1000){
				return this.weight/1000 + "kg";
			}else{
				return this.weight +"g";
			}
		}
	},
	methods: {
		
		onclick:function(name){
			this.title = name;
		},
		editStyle:function(){
			if(this.classname == "box2"){
				this.classname = "box";
			}else{
				this.classname = "box2";
			}
			
		},
		addAge:function(){
			this.age++;
		}

	}
}
